<template>
    <div class="loading-wrap">
        <div class="load-bg">
          <div class="flower"></div>
          <div class="flower"></div>
          <div class="flower"></div>
        </div>
    </div>
</template>

<script>
    export default {
        name: ""
    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
.loading-wrap{
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
}
  .load-bg{
    position: absolute;
    top:50%;
    left: 50%;
    width: 3.12rem;
    height: 2.35rem;
    transform:translate(-50%,-50%);
    background: url("../common/image/sprite.icons.png") no-repeat;
    background-size: 941px auto;
    background-position: 0 -1.8rem;
    font-size: 0;
  }
  .flower{
    width: 0.666666rem;
    height: 0.8rem;
    background: url("../common/image/sprite.icons.png");
    background-position: -5.55rem -8.42rem;
    background-size: 941px auto;
    display: inline-block;
    margin-top: 0.83rem;
    opacity: 1;
  }
.flower:first-child{
  margin-left: 0.23rem;
  -webkit-animation: flower1 1s infinite;
  animation: flower1 1s infinite;
}
.flower:nth-child(2){
  margin: 0 0.11rem;
  -webkit-animation: flower2 1s infinite;
  animation: flower2 1s infinite;
}
.flower:last-child{
  -webkit-animation: flower3 1s infinite;
  animation: flower3 1s infinite;
}
@-webkit-keyframes flower1 {
  0%,24.9%{
    opacity: 0;
  }
  25%, 99.9% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes flower1 {
  0%,24.9%{
    opacity: 0;
  }
  25%, 99.9% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
  }

@-webkit-keyframes flower2 {
  0%,49.9% {
    opacity: 0;
  }
  50%, 99.9% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
  }
@keyframes flower2 {
    0%,49.9% {
      opacity: 0;
    }
    50%, 99.9% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }

@-webkit-keyframes flower3 {
  0%, 74.9% {
    opacity: 0;
  }
  75%, 99.9% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
  }
@keyframes flower3 {
    0%, 74.9% {
      opacity: 0;
    }
    75%, 99.9% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
</style>



