import Vue from 'vue'
import Router from 'vue-router'
import index from 'components/index'

Vue.use(Router)

export const router= new Router({
  routes: [
    {
      path: '/',
      name: '',
      redirect:'/index',
    },
    {
      path:'/index',
      component: resolve => { require(['components/index.vue'], resolve); },
    },
  ]
})



