<template>
    <div class="index-wrap">
      <!--<div class="test"></div>-->
      <div class="want-play-wrap">
         <span class="title">我也要玩</span>
         <input type="file" accept="image/*"  class="btn"  @change="uploadInit" ref="upload">
      </div>
      <transition name="hint"><div class="hint" v-show="showHint">照片不符合噢！</div></transition>
      <loading v-show="loadingOnOff"></loading>
    </div>
</template>

<script>
  import  loading from "base/loading"
    export default {
      components:{
        loading
      },
      data(){
        return {
          isSuc:false,
          imgSrc:'',
          showHint:false,
          loadingOnOff:false,
          options:{
            token:'',
            imgSrc:'',
            uploadTimer:'',
            createCanvasTimer:'',
          }
        }
      },
      methods:{
        uploadInit(){
          let uploadInput=this.$refs.upload;
          let windowURL = window.URL || window.webkitURL;
          let dataURL;
          let _this=this;
          let file = uploadInput.files[0];
          if (uploadInput && uploadInput.files && uploadInput.files[0]) {
            dataURL = windowURL.createObjectURL(uploadInput.files[0]);
            this.imgSrc=dataURL;
            // uploadInput.value='';
          }
          if(!this.isSuc){
            this.showHint=true;
            setTimeout(function () {
              _this.showHint=false;
            },2100)
          }
          let fileType =['image/jpeg','image/jpg','image/png'];
          let filename = file.name;
          let type = ('image/'+filename.substring(filename.lastIndexOf('.')+1,filename.length)).toLocaleLowerCase();
          if ((fileType.join().toString()).indexOf(type) < 0) {
            alert('只允许上传JPG、PNG格式图片！');
            return false;
          }
          this.uploadCheck(file);
          this.$refs.upload.value = '';
        },
        uploadCheck(file){
          let token = this.options.token;
          let _this=this
          let startTimer = new Date().getTime();{
            if(token==''){
              this.getUploadToken(function(){
                _this.uploadImg(file)
              });
            }else{
              _this.uploadImg(file);
            }
          }
        },
        getUploadToken(cbk){
          let _this = this,callback = cbk || function(){};
          this.$jsonp(process.env.API_HOST+'public/index/qn_token').then((res)=>{
            // console.log(res)
            this.options.token = res;
              callback();
          })
        },
        uploadImg(file){
          let Qiniu_UploadUrl = "http://up.qiniu.com",
            ops = this.options,
            token = ops.token;
          let startTimer = new Date().getTime();
          let formData = new FormData();
          let _this=this
          formData.append('token', token.upload_token);
          formData.append('key', token.key);
          formData.append('file', file);
          let xhr = new XMLHttpRequest();
          // _this.setLoadingOnOff(true)
          xhr.open('POST', Qiniu_UploadUrl, true);
          xhr.onreadystatechange = function(response) {
            // console.log(xhr.readyState)
            // console.log(xhr.responseText)
            // console.log(xhr.status)
            if(xhr.readyState == 4 && xhr.status == 200 && xhr.responseText != "") {
              let imgSrc='http://mtapplet.meitudata.com/'+token.key ;
              //验证图片是否通过
              _this.handleImg(imgSrc)

            }else if(xhr.status != 200 && xhr.responseText) {
              _this.setLoadingOnOff(false);
              alert('上传出错了，请返回重试~');
            }
            // this.options.token='';//一个token只能用一次
          };
          xhr.send(formData);
        },
        handleImg(imgSrc){
          this.$jsonp(process.env.API_HOST+'rolleye/livephoto/index',{pic:imgSrc}).then(res=>{

          })
        }
      },

      created(){
      }

    }
</script>

<style  lang="scss" rel="stylesheet/scss">
  body{
    position: absolute;
    text-align: center;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: url("../common/image/bg-home.jpg") no-repeat;
    background-size: 100% 100%;
  }
  .test{
    width: 100%;
    height: 207px;
    background: red;
  }
  .index-wrap{
    position: absolute;
    text-align: center;
    width: 100%;
    height: 100%;
    /*overflow: hidden;*/
    /*background: url("../common/image/bg-home.jpg") no-repeat;*/
    /*background-size: 100% 100%;*/
  }
  .want-play-wrap{
    position: relative;
    margin: 543px auto 0;
    width: 195px;
    height: 42px;
    line-height: 42px;
    background: #ff8930;
    color: #fff;
    .title{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
    }
    .btn{
      opacity: 0;
    }
  }
  .hint{
    position: absolute;
    width: 80%;
    height: 40px;
    left: 50%;
    top: 50%;
    background: rgba(0,0,0,0.4);
    color: #fff;
    text-align: center;
    line-height: 40px;
    font-size: 16px;
    border-radius: 6px;
    transform: translate(-50%,-50%);
  }
  .hint-enter-active, .hint-leave-active{
    opacity: 1;
    transition: all 2.4s
  }
  .hint-enter, .hint-leave-to{
    opacity: 0;
  }
</style>



